<script>
import { mapActions,mapGetters,mapState} from 'vuex'

export default {
  name: "task",
  data() {
    return {
      msg: "工作周报",
      message: "",
      currentFirstDate: "",
      movingDom: "",
      taskIsMoving: false,
      extraIsMoving: false,
      plansIsMoving: false,
			schedules:{},
      newItemIndex:null,
      twinkleOut:null,
      isSubmiting:false
    };
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
	beforeRouteLeave (to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
		this.updateSchedules(this.schedules);
		next();
  },
  mounted() {
    this.currentWeekDays = this.getWeekDays(new Date());

    let nextDate = this.addDate(this.currentWeekDays[0]['dateStr'],7);   

    this.nextWeekDays = this.getWeekDays(new Date(nextDate));

		this.setTitle('工作周报');
		if(this.$store.state.report.schedules.tasks && this.$store.state.report.schedules.tasks.length >0){
			this.schedules = this.$store.state.report.schedules;
			return;		
    }
    /*
	this.schedules = {
		tasks:[],
		extraTasks:[],
		plans:[]
  };
  */
this.schedules = this.getSampleData();

console.log('this.schedules',this.schedules);
/*
    let sampleextraTasks = this.getSampleData('extraTasks');
    this.schedules.extraTasks = [sampleextraTasks];
    
    let samplePlans = this.getSampleData('plans');
    this.schedules.plans = [samplePlans];
 */
this.weekNumber = this.getWeekOfYear();

this.getSchedules();

  },
  methods: {
		...mapActions([
      'setTitle',
      'updateSchedules',
			'setShowSnackbar'
    ]),
    dosubmit() {
      
      if( !this.checkForm() ){
          return;
      }
     
      return;
      
      if(this.schedules.extraTasks.length > 0 && this.schedules.extraTasks[0]['title']){
        var extraTasksText = JSON.stringify(this.schedules.extraTasks);
      } else {
        var extraTasksText = '';
      }
  
      this.isSubmiting = true;

      var data = this.$qs.stringify({
        userId: this.userInfo.userId,
        userName:this.userInfo.username,
        weekNumber:this.weekNumber,
        tasks:JSON.stringify(this.schedules.tasks),
        plans:JSON.stringify(this.schedules.plans),
        extraTasks:extraTasksText    
      });
   
      this.$axios.post('index.php/report/postSchedules',
			data,
			).then(response => {
        if(response.data.msg == 'ok'){

          this.setShowSnackbar('周报已提交');

          this.$router.push('/report'); 
			    setTimeout(()=>{
					this.setShowSnackbar(false);
					},1200)
               
        } else {
          this.setShowSnackbar(response.data.msg);
        }
      }).catch(function (error) {
					console.log(error);
				 });

		
    },
    checkForm(){

    //检查下周计划    
    if(this.schedules.plans.length < 1){
        	this.setShowSnackbar('请填写下周计划');		
        return false;
      } else {
          this.schedules.plans.forEach(element => {
            if(element['title'] && element['title'].replace(/(^\s*)|(\s*$)/g,"") ){
            console.log('下周计划 ok');
             
            } else {
              this.setShowSnackbar('下周计划没有填写完整');	
              return false;
            }
          });

      }
    //检查本周任务
      if(this.schedules.tasks.length < 1){
        		this.setShowSnackbar('请填写本周任务');		
        return false;
      } else {
          this.schedules.tasks.forEach(element => {
            if(element['title'] && element['percent'] && element['title'].replace(/(^\s*)|(\s*$)/g,"")){         
             console.log('本周任务 ok');
            } else {
              this.setShowSnackbar('本周工作信息不完整,请检查');       
              document.body.scrollTop = 0;
              document.documentElement.scrollTop = 0;
              return false;

            }
          });
      }
     //检查额外任务
     if(this.schedules.extraTasks.length > 0){
          this.schedules.extraTasks.forEach(element => {
            if(element['title'] && element['title'].replace(/(^\s*)|(\s*$)/g,"")){
             
             
            } else {
              this.setShowSnackbar('额外任务信息不完整,请检查'); 
              return false;
          
            }
          });
     }

     return true;
    },
    getSchedules(){
 
      var data = this.$qs.stringify({
        userId: this.userInfo.userId,
        weekNumber:this.weekNumber - 1
      });
    	this.$axios.post('index.php/report/getSchedules',
			data,
			).then(response => {
        if(response.data){
          this.renderData(response.data);      
					//提交mutation到Store
					//this.$store.commit('updateinfo_req',response.data); 
         
        } else {
          this.setShowSnackbar('错误#02');
        }
      }).catch(function (error) {
					console.log(error);
				 });


    },
    renderData(resdata){
      if(resdata.plans){
           this.schedules.tasks = JSON.parse(resdata.plans);
          this.schedules.tasks.forEach(element => {
              element['finishDate'] = element['scheduledDate'];
              //element['percent'] = '50';
              if(!element['remark'] || element['remark'] == 'null'){
                element['remark'] = ' ';
              }
          });
              
      } else {
            let sampleTask = this.getSampleData('tasks');
            this.schedules.tasks = [sampleTask];
                   
      }

    },
    addTask(taskType,index) {
			if(!taskType){
				return;
			}
      if (index == 0 || index) {
        this.schedules[taskType].splice(
          index + 1,
          0,
          JSON.parse(JSON.stringify(this.schedules[taskType][index]))
        );
        this.twinkle(index + 1,taskType);

        return;
      }

      //let tmpItem = JSON.stringify(this.schedules[taskType][this.schedules[taskType].length - 1]);
      //tmpItem = JSON.parse(tmpItem);

      let tmpItem = this.getSampleData(taskType);


      if(tmpItem.scheduledDate){
          let tmpSd = new Date(tmpItem.scheduledDate);
          if (tmpSd.getDay() != 0) {
              tmpItem.scheduledDate = this.addDate(tmpItem.scheduledDate, 1);
          }
      }
      if(tmpItem.finishDate){
          let tmpfd = new Date(tmpItem.finishDate);
          if (tmpfd.getDay() != 0) {
            tmpItem.finishDate = this.addDate(tmpItem.finishDate, 1);
          }
      }
      tmpItem.title = '';
      tmpItem.title

      this.schedules[taskType].push(tmpItem);
    },
    removeTask(taskType,index) {
      if (index != 0 && !index) {
        return;
      }
      this.schedules[taskType].splice(index, 1);
    },
    clearTask(taskType,index) {
      this.schedules[taskType][index].title = "";
      this.schedules[taskType][index].percent = null;
      this.schedules[taskType][index].remark = "";
    },
    getSampleData(type){

      if(type == 'tasks'){
        return {
                      title: "",
                        scheduledDate: this.currentWeekDays[0].dateStr,
                        percent: null,
                        finishDate: this.currentWeekDays[0].dateStr,
                        remark: ""
                      }

      }
      if(type == 'plans'){
        return {
                  title: "",
                  scheduledDate: this.nextWeekDays[0].dateStr,
                  remark: ""
                }


      }
      if(type == 'extraTasks'){
        return  {
                  title: " ",
                  hours: "2",
                  finishDate: this.currentWeekDays[0].dateStr,
                  remark: " "
                }


      }
      return {
          tasks:[
                  {
                      title: " ",
                        scheduledDate: this.currentWeekDays[0].dateStr,
                        percent: null,
                        finishDate: this.currentWeekDays[0].dateStr,
                        remark: " "
                      }
                    ],          
          extraTasks:[
                
              ],
          plans:[
                {
                  title: " ",
                  scheduledDate: this.nextWeekDays[0].dateStr,
                  remark: " "
                }
              ]
          }
    },
    formatDate(date) {
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var week = ["日", "一", "二", "三", "四", "五", "六"][date.getDay()];
      
      //如果不够2位,数字前补0
      //month = (Array(2).join('0') + month).slice(-2)
      //day = (Array(2).join('0') + day).slice(-2)
      return {
        dateStr: year + "-" + month + "-" + day,
        week: week,
        index: date.getDay()
      };
    },
    getWeekDays(date) {
      var week = date.getDay() - 1;
      date.setDate(date.getDate() + week * -1);
      this.currentFirstDate = new Date(date);

      let weekDays = [];

      for (var i = 0; i < 7; i++) {
        var weekDay = "";
        if (i == 0) {
          weekDay = this.formatDate(date);
        } else {
          date.setDate(date.getDate() + 1);
          weekDay = this.formatDate(date);
        }

        weekDays.push(weekDay);
      }
      return weekDays;
    },    
    getWeekOfYear() {
      var today = new Date();
      var firstDay = new Date(today.getFullYear(),0, 1);
      var dayOfWeek = firstDay.getDay(); 
      var spendDay= 1;
      if (dayOfWeek !=0) {
        spendDay=7-dayOfWeek+1;
      }
      firstDay = new Date(today.getFullYear(),0, 1+spendDay);
      var d =Math.ceil((today.valueOf()- firstDay.valueOf())/ 86400000);
      var result =Math.ceil(d/7);
      return result+1;
    },
    addDate(date, days) {
      console.log('date',date);
      var d = new Date(date.replace(/-/g,"/") );
      d.setDate(d.getDate() + days);
      var m = d.getMonth() + 1;
      return d.getFullYear() + "-" + m + "-" + d.getDate();
    },
    //拖放操作
    drag: function(event,tabletype) {
      this.movingDom = event.currentTarget;
      this[tabletype] = true;
    },
    drop: function(event,tabletype) {
      event.preventDefault();
      this[tabletype] = false;
			var taskType = '';
			switch(tabletype){
				case 'taskIsMoving':
					taskType = 'tasks';
					break;
				case 'extraIsMoving':
					taskType = 'extraTasks';
					break;
				case 'plansIsMoving':
					taskType = 'plans';
					break;
				default:
					return;
			
			}

      var curNode = event.target.parentNode;
      while (curNode.tagName != "TR") {
        //result.push(x);
        curNode = curNode.parentNode;
      }
			while (this.movingDom.tagName != "TR") {
        this.movingDom = this.movingDom.parentNode;
      }
    
      let targetId = curNode.getAttribute("id");
      let movingId = this.movingDom.getAttribute("id");

      if (Math.abs(targetId - movingId) >= 2) {
        //数组交换位置
        //this.tasks[targetId] = this.schedules[taskType].splice(movingId, 1, this.schedules[taskType][targetId])[0];

        //移动数组
        if (movingId > targetId) {
          let tempVal = JSON.stringify(this.schedules[taskType][movingId]);
          this.schedules[taskType].splice(movingId, 1);
          this.schedules[taskType].splice(targetId, 0, JSON.parse(tempVal));
          this.twinkle(targetId,taskType);	
        } else {
          this.twinkle(targetId-1,taskType);	
          this.schedules[taskType].splice(targetId, 0, this.schedules[taskType][movingId]);
          this.schedules[taskType].splice(movingId, 1);
        }
        
      }
    },
    allowDrop: function(event) {
      event.preventDefault();
    },
    shuffle() {
      this.tasks.reverse();
    },
    twinkle(index,taskType){
      clearTimeout(this.twinkleOut);
      this.newItemIndex = taskType + index;        
        var _this = this;
       this.twinkleOut =  setTimeout(()=>{
            _this.newItemIndex = null;
        },2000);
    },
    allDone(){
       this.setShowSnackbar('还没做');
          setTimeout(()=>{
					this.setShowSnackbar(false);
					},1000)    
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.task {
  .list-enter-active,
  .list-leave-active {
    transition: all 0.3s;
  }
  .list-enter,
  .list-leave-to {
    opacity: 0;
    transform: translateY(30px);
 
  }
  .list-move {
    transition: all 1s;
  }
  .list-move-active {
    position: absolute;
  }
  .report-table li {
  }


  .action {
    margin-bottom: 25px;
  }
  .text-r {
    text-align: right;
  }
  .report-table {
    margin: 0 auto;
    p.move:hover {
      //background: #eee;
      cursor: move;
    }
  }
	.report-table.move {
			border-collapse:collapse;
			tr.item {
				border: 2px dashed #ccc;
			}
	
	}


  select {
    height: 30px;
    line-height: 30px;
    padding-left: 10px;
    /*appearance: none;*/
    font-size: 14px;
  }
  select option {
    line-height: 30px;
  }
  input.drop:hover {
  }
}
</style>



